"use client";

import { ArrowRight, Code2, Cpu, Dna, Check } from "lucide-react";
import { agencyConfig } from "../data/agencyConfig";
import { useState, useEffect } from "react";

export default function Hero({ blockData }) {
    const preTitle = blockData?.name || "Jasa Pembuatan Website Katering & UMKM";
    const titleText = blockData?.title || "Bikin Bisnis Lebih Laris dengan Website Otomatis";
    const subtitle = blockData?.subtitle || "Jangan biarkan pelanggan kabur karena ribet tanya harga manual. Tampilkan menu, harga, dan portfolio Anda secara profesional 24/7.";
    const ctaText = blockData ? blockData.ctaText : "Lihat Paket Hemat";
    const ctaLink = blockData ? blockData.ctaLink : "#pricing";
    const secondaryCtaText = blockData ? blockData.secondaryCtaText : "Konsultasi Dulu";
    const secondaryCtaLink = blockData ? blockData.secondaryCtaLink : `https://wa.me/${agencyConfig.brand.whatsappNumber}`;
    
    // Parse checklists (comma separated or newline)
    const rawChecklists = blockData ? blockData.checklists : "Terima Beres, Tanpa Biaya Bulanan, Proses Cepat 3 Hari";
    const checklistItems = rawChecklists ? rawChecklists.split(/,|\n/).map(c => c.trim()).filter(Boolean) : [];

    const words = titleText.split(' ');
    let firstPart = titleText;
    let highlightPart = "";
    if (words.length > 2) {
        highlightPart = words.splice(-2).join(' ');
        firstPart = words.join(' ');
    } else {
        firstPart = "";
        highlightPart = titleText;
    }

    // Typing effect for the code window
    const [codeText, setCodeText] = useState("");
    const fullCode = `const developer = {
  name: "Krisdiantoro",
  skills: ["React", "Next.js", "AI", "Bio"],
  focus: "Intelligent Web Apps",
  status: "Available to Build"
};

// Initializing neural engine...
await engine.start();
console.log("System Online");`;

    useEffect(() => {
        let i = 0;
        const typingInterval = setInterval(() => {
            setCodeText(fullCode.substring(0, i));
            i++;
            if (i > fullCode.length) clearInterval(typingInterval);
        }, 30);
        return () => clearInterval(typingInterval);
    }, []);

    return (
        <section className="relative overflow-hidden bg-slate-50 pt-20 pb-24 lg:pt-32 lg:pb-32">
            {/* Background Gradients */}
            <div className="absolute top-0 left-0 w-full h-full overflow-hidden pointer-events-none z-0">
                <div className="absolute -top-[20%] -right-[10%] w-[70%] h-[70%] bg-blue-300 rounded-full mix-blend-multiply filter blur-[120px] opacity-20 animate-blob"></div>
                <div className="absolute top-[20%] -left-[10%] w-[60%] h-[60%] bg-indigo-300 rounded-full mix-blend-multiply filter blur-[120px] opacity-20 animate-blob animation-delay-2000"></div>
            </div>

            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
                <div className="grid grid-cols-1 lg:grid-cols-12 gap-16 items-center">
                    
                    {/* Left Column: Content */}
                    <div className="lg:col-span-6 flex flex-col items-start text-left">
                        {preTitle && (
                            <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-white border border-blue-100 shadow-sm text-blue-700 text-sm font-semibold mb-8 hover:shadow-md transition-shadow">
                                <span className="relative flex h-2 w-2">
                                    <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-blue-400 opacity-75"></span>
                                    <span className="relative inline-flex rounded-full h-2 w-2 bg-blue-500"></span>
                                </span>
                                {preTitle}
                            </div>
                        )}

                        <h1 className="text-5xl md:text-6xl font-extrabold text-slate-900 tracking-tight mb-6 leading-[1.15]">
                            {firstPart} <span className="text-transparent bg-clip-text bg-gradient-to-r from-blue-600 to-indigo-600">{highlightPart}</span>
                        </h1>

                        <p className="text-xl text-slate-600 mb-10 leading-relaxed max-w-2xl">
                            {subtitle}
                        </p>

                        <div className="flex flex-col sm:flex-row gap-4 w-full sm:w-auto mb-12">
                            {ctaText && (
                                <a
                                    href={ctaLink}
                                    className="w-full sm:w-auto px-8 py-4 my-auto bg-blue-600 text-white rounded-xl font-bold text-lg shadow-lg shadow-blue-600/20 hover:bg-blue-700 hover:shadow-blue-600/40 transition-all hover:-translate-y-1 flex items-center justify-center gap-2"
                                >
                                    {ctaText} <ArrowRight className="h-5 w-5" />
                                </a>
                            )}
                            {secondaryCtaText && (
                                <a
                                    href={secondaryCtaLink}
                                    target={secondaryCtaLink.startsWith('http') ? "_blank" : "_self"}
                                    rel={secondaryCtaLink.startsWith('http') ? "noopener noreferrer" : ""}
                                    className="w-full sm:w-auto px-8 py-4 bg-white text-slate-700 border border-slate-200 rounded-xl font-bold text-lg hover:border-slate-300 hover:bg-slate-50 transition-all flex items-center justify-center gap-2 shadow-sm my-auto"
                                >
                                    {secondaryCtaText}
                                </a>
                            )}
                        </div>

                        {checklistItems.length > 0 && (
                            <div className="flex flex-wrap items-center gap-x-6 gap-y-3 text-sm font-medium text-slate-600">
                                {checklistItems.map((item, idx) => (
                                    <div key={idx} className="flex items-center gap-2">
                                        <div className="flex items-center justify-center w-5 h-5 rounded-full bg-green-100 text-green-600">
                                            <Check className="h-3 w-3" />
                                        </div>
                                        <span>{item}</span>
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>

                    {/* Right Column: Visual Composition */}
                    <div className="lg:col-span-6 relative w-full h-[500px] lg:h-[600px] rounded-3xl hidden lg:block">
                        {/* Abstract Background Shape */}
                        <div className="absolute inset-0 bg-gradient-to-br from-slate-100/50 to-white/30 border border-white/60 rounded-3xl shadow-2xl overflow-hidden backdrop-blur-xl transform rotate-1 scale-105 transition-transform hover:rotate-0 hover:scale-100 duration-700">
                            
                            {/* Decorative grids */}
                            <div className="absolute inset-0" style={{ backgroundImage: 'radial-gradient(circle at 2px 2px, rgba(148, 163, 184, 0.15) 1px, transparent 0)', backgroundSize: '24px 24px' }}></div>

                            {/* Dev Component (Code Window) */}
                            <div className="absolute top-8 left-8 right-12 bg-slate-900 rounded-xl shadow-2xl border border-slate-700 overflow-hidden transform -rotate-2 hover:rotate-0 transition-all duration-500 z-20 hover:scale-105">
                                <div className="flex items-center px-4 py-3 bg-slate-800/80 border-b border-slate-700/50 backdrop-blur-md">
                                    <div className="flex gap-2">
                                        <div className="w-3 h-3 rounded-full bg-red-500/80"></div>
                                        <div className="w-3 h-3 rounded-full bg-yellow-500/80"></div>
                                        <div className="w-3 h-3 rounded-full bg-green-500/80"></div>
                                    </div>
                                    <div className="mx-auto text-xs text-slate-400 font-mono flex items-center gap-2">
                                        <Code2 className="h-3 w-3" /> main.ts
                                    </div>
                                </div>
                                <div className="p-5 font-mono text-sm leading-relaxed overflow-hidden h-48">
                                    <pre className="text-emerald-400 whitespace-pre-wrap">
                                        <code>{codeText}</code>
                                        <span className="animate-pulse inline-block w-2 sm:w-2.5 h-4 sm:h-5 bg-emerald-400 ml-1 translate-y-1"></span>
                                    </pre>
                                </div>
                            </div>

                            {/* AI Component (Neural Network visualization) */}
                            <div className="absolute bottom-12 right-6 bg-white/90 backdrop-blur-xl p-5 rounded-2xl shadow-xl border border-white z-30 transform hover:-translate-y-2 hover:scale-105 transition-all duration-500 group">
                                <div className="flex items-center gap-3 mb-4">
                                    <div className="p-2.5 bg-indigo-100 text-indigo-600 rounded-xl group-hover:bg-indigo-600 group-hover:text-white transition-colors duration-300">
                                        <Cpu className="h-5 w-5" />
                                    </div>
                                    <div>
                                        <p className="text-sm font-bold text-slate-800">AI Integration</p>
                                        <p className="text-[11px] text-slate-500 font-medium">Processing models...</p>
                                    </div>
                                </div>
                                <div className="flex justify-between items-end gap-1.5 h-10 px-2">
                                    {[1, 2, 3, 4, 5, 6].map((bar) => (
                                        <div key={bar} className="w-1.5 bg-indigo-100 rounded-t-sm relative overflow-hidden h-full">
                                            <div 
                                                className="absolute bottom-0 left-0 w-full bg-indigo-500 rounded-t-sm" 
                                                style={{ 
                                                    height: `${Math.random() * 60 + 40}%`,
                                                    animation: `pulse ${Math.random() * 1 + 0.5}s infinite alternate`
                                                }}>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>

                            {/* Biochemistry Component */}
                            <div className="absolute bottom-24 left-6 bg-white/90 backdrop-blur-xl p-4 rounded-2xl shadow-xl border border-white z-10 transform -rotate-6 hover:rotate-0 hover:scale-105 transition-all duration-500 group">
                                <div className="flex items-center gap-3">
                                    <div className="relative flex items-center justify-center p-2.5 bg-emerald-100 text-emerald-600 rounded-xl group-hover:scale-110 transition-transform duration-300">
                                        <Dna className="h-5 w-5 relative z-10" />
                                        <div className="absolute inset-0 bg-emerald-400 opacity-20 filter blur-md rounded-full animate-pulse z-0 hidden group-hover:block"></div>
                                    </div>
                                    <div className="pr-2">
                                        <p className="text-sm font-bold text-slate-800">Biochemistry</p>
                                        <p className="text-[11px] text-emerald-600 font-semibold">Research Data</p>
                                    </div>
                                </div>
                            </div>

                            {/* Floating decorative elements */}
                            <div className="absolute top-1/2 left-1/4 w-32 h-32 bg-indigo-400 rounded-full mix-blend-multiply filter blur-3xl opacity-30 animate-blob"></div>
                            <div className="absolute top-1/4 right-1/4 w-32 h-32 bg-emerald-400 rounded-full mix-blend-multiply filter blur-3xl opacity-30 animate-blob animation-delay-2000"></div>

                        </div>
                    </div>
                </div>
            </div>

            {/* Global Keyframes for the inline animation styles */}
            <style jsx global>{`
                @keyframes pulse {
                    0% { opacity: 0.6; transform: scaleY(0.8); }
                    100% { opacity: 1; transform: scaleY(1); }
                }
            `}</style>
        </section>
    );
}
